.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo__img {
  justify-content: center;
  align-items: center;
  margin-right: 50px;
  display: flex;
}

.nav {
  height: 60px;
  line-height: 60px;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  height: 60px;
  list-style-type: none;
  position: relative;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  color: #060b35;
  text-align: center;
  text-transform: uppercase;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: block;
  position: relative;
}

.is-active, .nav__link:hover {
  color: #00acdc;
}

.is-active:after {
  content: "";
  text-transform: uppercase;
  border-bottom: 4px solid #00acdc;
  border-radius: 8px;
  width: 100%;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  position: absolute;
  bottom: 0;
  left: 0;
}
